.App {
    display: grid;
    grid-template-columns: 1fr 350px;
    grid-template-rows: 80px 50vh 1fr 70px;
    grid-template-areas: 'nav-bar nav-bar'
        'header header'
        'projetos-block info-block'
        'footer footer';
    background-color: #395B64;
    min-width: 600px;
}

@media (max-width: 1250px){
    .App {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 80px 50vh 1fr 400px 70px;
        grid-template-areas: 'nav-bar'
            'header'
            'projetos-block'
            'info-block'
            'footer';
        background-color: #395B64;
    }
}


@media (max-width: 990px){
    .App {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 80px 50vh 1fr 400px 70px;
        grid-template-areas: 'nav-bar'
            'header'
            'projetos-block'
            'info-block'
            'footer';
        background-color: #395B64;
    }
}