.projetos-block {
    grid-area: projetos-block;
    color: #fff;
    margin: 10px 20px;
    background-color: #F5F2E7;
    border-radius: 6px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 60px 1fr;
    grid-template-areas: 'projetos-header'
        'cards-block';
    min-width: 400px;
    min-height: 900px;
}

.projetos-header {
    background-color: #2C3333;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    text-transform: uppercase;
    grid-area: projetos-header;
}

.projetos-header h1 {
    margin: 0;
}

.cards-block {
    grid-area: cards-block;
    gap: 20px;
    margin: 15px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    justify-items: center;
}

@media (max-width: 1250px){
    .projetos-block {
        grid-area: projetos-block;
        color: #fff;
        margin: 10px 20px;
        background-color: #F5F2E7;
        border-radius: 6px;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 60px 1fr;
        grid-template-areas: 'projetos-header'
            'cards-block';
    }
    .cards-block {
        grid-area: cards-block;
        gap: 20px;
        margin: 15px;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(4, 1fr);
        justify-items: center;
    }      
}

@media (max-width: 990px){
    .projetos-block {
        grid-area: projetos-block;
        color: #fff;
        margin: 10px 20px;
        background-color: #F5F2E7;
        border-radius: 6px;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 60px 1fr;
        grid-template-areas: 'projetos-header'
            'cards-block';
    }
}