.info-block {
    grid-area: info-block;
    margin: 10px 20px 10px 0px;
    background-color: #F5F2E7;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    color: #000;
    text-align: center;
    min-height: 900px;
}

.section{
    display: flex;
    flex-direction: column;
}

.info-header {
    color: #fff;
    background-color: #2C3333;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    text-transform: uppercase;
}

.info-header h1 {
    margin: 0;
}

.apresentation, .resume, .profile {
    width: 90%;
    align-self: center;
    margin: 10px;
}

.profile .image {
    border-radius: 50%;
    background-image: url('../assets/imgs/profilePicture.jpg');
    background-position: center;
    background-size: cover;
    height: 300px;
}

.resume h3 {
    margin: 10px;
}

.apresentation p, .resume p{
    background-color: #fff;
    padding: 10px;
    text-align: justify;
    border: solid 1px #ccc;
}

@media (max-width: 1250px){
    .info-block {
        margin-left: 20px;
        min-height: 0px;
    }

    .section{
        display: flex;
        flex-direction: row;
    }
    
    .apresentation, .resume, .profile {
        width: 40%;
        align-self: center;
        margin: 10px;   
    }

    .profile .image {
        border-radius: 50%;
        background-image: url('../assets/imgs/profilePicture.jpg');
        background-position: center;
        background-size: cover;
        height: 300px;
    }

    .resume h3 {
        margin: 10px;
    }

    .apresentation p, .resume p{
        background-color: #fff;
        padding: 10px;
        text-align: justify;
        border: solid 1px #ccc;
    }     
}

@media (max-width: 680px){
    .apresentation p, .resume p{
        font-size: 14px;
        background-color: #fff;
        padding: 10px;
        text-align: justify;
        border: solid 1px #ccc;
    }

    .profile .image {
        height: 200px;
    }
}