.nav-bar {
    grid-area: nav-bar;
    background-color: #2C3333;
    color: #fff;
    border-bottom: solid 1px #fff;
    display: flex;
    justify-content: space-between;
    padding: 0px 30px;
    align-items: center;
    font-size: 18px;
    min-width: 400px;
}

.logo {
    height: 80%;
    width: 200px;
}

.links a {
    text-decoration: none;
    color: #fff;
    margin: 0px 10px;
}

.links a:hover {
    color: #aaa;
}