.card {
    color: black;
    max-width: 400px;
    min-width: 300px;
    width: 400px;
    height: 400px;
    background-color: #fff;
    box-shadow: 1px 1px 7px #aaa;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card h2 {
    margin: 20px;
    padding: 5px;
    border-bottom: black solid 1px;
}

.card img {
    width: 90%;
    border: #ddd solid 1px;
    margin: 5px;
}

.card-description {
    text-align: center;
    height: 70px;
}

.card a {
    color: #fff;
    text-decoration: none;
    background-color: #2666CF;
    padding: 15px;
}

.card a:hover {
    background-color: #266699;
}

