.header {
    grid-area: header;
    color: white;
    background-image: url('../assets/imgs/forest.jpg');
    background-position: center;
    background-size: cover;
    min-width: 400px;
    min-height: 330px;
}

.text-block {
    background: #395B64;
    background: linear-gradient(180deg, rgba(57,91,100,0.5) 0%, rgba(57,91,100,1) 95%);
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.text-block h1{
    margin: 0;
}

.welcome-block {
    width: 500px;
    padding: 0px 10px;
}

.stacks-block {
    text-align: center;
}

.stacks-block div {
    margin: 1em;
}

.stacks-block i{
    font-size: 50px;
    margin: 10px;
}

@media (max-width: 875px){
    .text-block {
        background: #395B64;
        background: linear-gradient(180deg, rgba(57,91,100,0.5) 0%, rgba(57,91,100,1) 95%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }   
}